.app-wrapper {
    display: grid;
    grid-template-areas:
            "h h h"
            "q q q"
            "n w c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10px 10fr;
    width: 1200px;
    margin: 0 auto;
}


.app-wrapper-content {
    grid-area: c;
    background-color: #f6c44f;;
}
